"use client";
import { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { useLocale, useTranslations } from "next-intl";
import { getProducts } from '@/utils/data';
import { useInView } from "react-intersection-observer"; // 👈 الاستيراد

const Products = () => {
    const t = useTranslations('Products');
    const steps = getProducts(t);
    const [activeStep, setActiveStep] = useState(1);
    const [isManuallySelected, setIsManuallySelected] = useState(false);
    const [isAutoPlayStarted, setIsAutoPlayStarted] = useState(false);

    const current = steps.find((s) => s.id === activeStep);
    const locale = useLocale();
    const isArabic = locale === 'ar';

    // 👇 استخدام المراقبة على السكشن
    const { ref, inView } = useInView({
        triggerOnce: true,
        threshold: 0.4,
    });

    useEffect(() => {
        if (inView) {
            setIsAutoPlayStarted(true);
        }
    }, [inView]);

    useEffect(() => {
        if (!isAutoPlayStarted || isManuallySelected) return;

        const timer = setTimeout(() => {
            setActiveStep(prev =>
                prev >= steps.length ? 1 : prev + 1
            );
        }, 3000);

        return () => clearTimeout(timer);
    }, [activeStep, isAutoPlayStarted, isManuallySelected, steps.length]);

    const handleStepClick = (id: number) => {
        setActiveStep(id);
        setIsManuallySelected(true);
    };

    return (
        <section ref={ref} className="bg-white">
            <div className="lg:py-16 px-4 sm:px-6 lg:px-8 lg:max-w-screen-xl lg:mx-auto">
                <div className="container lg:mx-auto flex flex-col-reverse lg:flex-row items-center lg:gap-12">
                    {/* ... باقي الكود كما هو */}
                </div>
            </div>
        </section>
    )
};

export default Products;
